.read-note-card {
    background-color: #ffffffde;
    text-align: left;
    border-radius: 10px;
    border: 1px solid #00000066;
    padding: .75em;
    display: flex;
    flex-direction: column;
}

.read-note-card:hover {
    /* background-color: rgb(230, 252, 252); */
    box-shadow: 0px 0px 5px 1px rgb(79, 197, 226);
}

.note-title p {
    margin: 0;
    margin-bottom: .3em;
    font-size: 1.2em;
    font-weight: bold;
    white-space: pre-wrap;
    word-break: keep-all;
    overflow: hidden;
}

.note-content {
    flex: 1;
}

.note-content pre {
    white-space: pre-wrap;
    word-break: keep-all;
    margin: 0;
    overflow: hidden;
}

.note-footer {
    display: flex;
    justify-content: flex-end;
}

.icon {
    width: 1.5em;
    cursor: pointer;
}

.icon:hover {
    box-shadow: 0px 0px 5px 1px rgb(79, 197, 226);
}

.delete-icon {
    margin-top: 5px;
}